.header {
  background-color: #0077cc;
  color: white;
  padding: 20px;
  text-align: center;
}

.footer {
  background-color: #0077cc;
  color: white;
  text-align: center;
  padding: 10px;
  position: fixed;
  bottom: 0;
  width: 100%;
}

.home {
  padding: 20px;
  text-align: center;
}

/* App.css */
.App {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

/* Ensure all routes take full height */
.route-container {
  height: 100%;
  width: 100%;
  overflow: hidden;
}
