.book-container {
  position: relative;
  height: 90vh;
  transform-style: preserve-3d;
  transition: transform 0.6s ease;
  transform-origin: center;
  perspective: 2000px;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
}

.book-container.show-back {
  transform: rotateY(180deg);
}

.front-cover, .back-cover {
  position: absolute;
  height: 90vh;
  width: calc(90vh * 0.62);
  max-height: 90vh;
  max-width: 95vw;
  backface-visibility: hidden;
  transform-origin: center;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
  overflow: hidden;
}

.back-cover {
  transform: rotateY(180deg);
  display: flex;
  flex-direction: column;
  padding: 20px;
  overflow-y: auto;
}

.cover-image, .back-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.cover-content {
  position: absolute;
  top: 10%;
  left: 10%;
  right: 10%;
  text-align: center;
  z-index: 2;
  padding: 2vh;
}

.cover-content h1 {
  font-family: 'Times New Roman', serif;
  font-size: calc(2.2vh + 1vw);
  color: white;
  margin-bottom: 2vh;
  text-transform: uppercase;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
  line-height: 1.2;
  max-width: 90%;
  margin-left: auto;
  margin-right: auto;
  font-weight: bold;
}

.cover-content .author {
  font-family: 'Times New Roman', serif;
  font-size: calc(1.2vh + 0.5vw);
  color: white;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.8);
}

.back-content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 30px;
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
  z-index: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.back-content h2 {
  font-family: 'Times New Roman', serif;
  font-size: calc(1.3vh + 0.6vw);
  margin-bottom: 25px;
  text-align: center;
  text-transform: uppercase;
}

.synopsis {
  margin-top: 20px;
}

.synopsis p {
  font-family: 'Times New Roman', serif;
  font-size: calc(0.85vh + 0.425vw);
  line-height: 1.5;
  margin-bottom: 20px;
  text-align: justify;
}

.publisher-info {
  text-align: center;
  font-family: 'Times New Roman', serif;
  font-size: calc(0.7vh + 0.35vw);
  opacity: 0.9;
  margin-top: auto;
  padding: 0 30px;
}

.book-buttons {
  position: absolute;
  bottom: 40px;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  gap: 20px;
  z-index: 2;
}

.book-button {
  padding: 12px 24px;
  font-size: calc(0.8vh + 0.4vw);
  color: white;
  background: rgba(0, 0, 0, 0.6);
  border: 2px solid rgba(255, 255, 255, 0.5);
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

.book-button:hover {
  background: rgba(0, 0, 0, 0.8);
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.book-buttons.back-buttons {
  bottom: 20px;
  margin-top: 20px;
}

@media (max-width: 768px) {
  .front-cover, .back-cover {
    width: 90vw;
    height: calc(90vw * 1.6);
  }

  .book-buttons {
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }

  .book-button {
    width: 80%;
    padding: 10px;
  }

  .back-content {
    padding: 20px;
  }

  .synopsis p {
    font-size: 14px;
  }
}
